import { FingerprintAIO } from '@ionic-native/fingerprint-aio';
import { Storage } from '@ionic/storage';
import {Camera} from 'ionic-native';
import { QrResultPage } from './../qr-result/qr-result';
import { BarcodeScanner, BarcodeScannerOptions } from '@ionic-native/barcode-scanner';
import { Component } from '@angular/core';
import { NavController, NavParams, AlertController } from 'ionic-angular';
import {Platform} from 'ionic-angular';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

  public data: string

  constructor(public platform: Platform, public navCtrl: NavController, public navParams: NavParams, private alertCtrl: AlertController, private barcodeScanner: BarcodeScanner, public storage: Storage, private finger: FingerprintAIO) {
    this.storage = navParams.get('storage');
  }

  ionViewDidLoad() {
    this.storage.get('first').then(bool => {
      if(bool) {
        if (this.finger.isAvailable()){
          this.storage.set('unavailable', false);
          console.log(bool);
          this.faio();
        } else {
          this.storage.set('first', false);
          this.storage.set('unavailable', true);
          this.storage.set('faio', false);
        }
      }
    })
  }

  popup() {
    let alert = this.alertCtrl.create({
      title: 'Erro',
      subTitle: "Contedo no  uma carta.",
      buttons: ['Ok']
    });
    alert.present();
  }

  faio() {
    this.storage.set('first', false);
    let alert = this.alertCtrl.create({
      title: 'Touch ID',
      message: 'Deseja ativar o Touch ID?',
      buttons:[
        {
          text: 'No',
          role: 'cancel',
          handler: () => {
            console.log('No clicked');
          }
        },
        {
          text: 'Sim',
          handler: () => {
            console.log('Sim clicked');
            this.storage.set('faio', true);
          }
        }
      ]
    })
    alert.present()
  }

  scan() {
    this.platform.ready().then(() => {
      this.barcodeScanner.scan().then((barcodeData) => {
        this.data = barcodeData.text;
        this.openItem(barcodeData.text);
      }, (err) => {
        let alert = this.alertCtrl.create({
          title: 'Erro',
          subTitle: err,
          buttons: ['Ok']
        });
        alert.present();
      })
    })
  }

  openItem(text: String) {
    this.navCtrl.push(QrResultPage, {
      contents: text,
      storage: this.storage
    });
  }
}



